.TouchItem {
	width: 7.5em;
	height: 7.5em;
	position: absolute;
	-webkit-transform: translate(-50%, -50%);
	    -ms-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}

.TouchItem::before {
	content: '';
	display: block;
	background-color: #09c;
	border-radius: 50%;
	width: 100%;
	height: 100%;
	-webkit-animation: animation-enter 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
	        animation: animation-enter 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

@-webkit-keyframes animation-enter {
	from {
		-webkit-transform: scale(0.5);
		        transform: scale(0.5);
	}

	to {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}

@keyframes animation-enter {
	from {
		-webkit-transform: scale(0.5);
		        transform: scale(0.5);
	}

	to {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}



